import styles from "./Skin.module.css";
import { BiShoppingBag } from "react-icons/bi";
import { IoCloseOutline } from "react-icons/io5";
import { FaBars } from "react-icons/fa";
import {Routes, Route, useNavigate} from 'react-router-dom';
import axios from 'axios';
import { useEffect, useState } from 'react';
import { useDispatch } from "react-redux";
import { setProductDetail } from "../store";




function Skin({product, setProduct}) {

   let [loding, setLoding] = useState(false);
   let navigate = useNavigate();
   let [firstBtn, setFirstBtn] = useState(true);
   
   useEffect(() => {
      setLoding(true);
      axios.get(`https://raw.githubusercontent.com/JiyunNam0/frontend/main/aesop/skindata1.json`)
      .then((결과) => {
         let data = 결과.data;
         setProduct(data);
         setLoding(false);
      })
      .catch((error) => {
         console.log('오류발생' + error);
         setLoding(false);
      })
   }, []);



   return(
      <>

      <Header navigate={navigate}/>

      {
         loding == true ? <img src={process.env.PUBLIC_URL + '/images/loding.gif'} /> : null
      }
   
      <div class={styles.main_skin_layout}>
         <h3>SKIN</h3>
         <div className={styles.top_button_area}>
            <button className={styles.name_short} onClick={() => {
               let copy = [...product];
               copy.sort((a, b) => {
                  if(a.title > b.title) {
                     return 1;
                  } else {
                     return -1;
                  }
               });
               setProduct(copy);
            }}>Name Short</button>
            <button className={styles.low_price_sort} onClick={() => {
               let copy = [...product];
               copy.sort((a, b) => {
                  if(a.price > b.price) {
                     return 1;
                  } else {
                     return -1;
                  }
               });
               setProduct(copy);
            }}>Low Price Sort</button>
            <div className={styles.etc}></div>
         </div>
         <div class={styles.skin_product}>
            <ul>
               {
                  product.map(function(a, i) {
                     return (
                        <Product product={product[i]} i={i} navigate={navigate}/>
                     )
                  }) 
               }
            </ul>
         </div>

         <div class={styles.next_page_button}>
            <ul>
               <li>
                  <button className={firstBtn ? styles.active : ''} onClick={() => {
                     axios.get(`https://raw.githubusercontent.com/JiyunNam0/frontend/main/aesop/skindata1.json`)
                     .then((결과) => {
                       let copy = [...결과.data];
                       setProduct(copy);
                     })
                  }}>1</button>
               </li>
               <li>
                  <button onClick={() => {
                     axios.get(`https://raw.githubusercontent.com/JiyunNam0/frontend/main/aesop/skindata2.json`)
                     .then((결과) => {
                       let copy = [...결과.data];
                       setProduct(copy);
                       setFirstBtn(false);
                     })
                  }}>2</button>
               </li>
               <li>
                  <button onClick={() => {
                     axios.get(`https://raw.githubusercontent.com/JiyunNam0/frontend/main/aesop/skindata3.json`)
                     .then((결과) => {
                       let copy = [...결과.data];
                       setProduct(copy);
                     })
                  }}>3</button>
               </li>
            </ul>
         </div>
      </div>

      </>
   )
}

function Header({navigate}) {
   
   let [isMenuOpen, setIsMenuOpen] = useState(false);

   useEffect(() => {
      if (isMenuOpen) {
        document.body.style.overflow = 'hidden';
      } else {
        document.body.style.overflow = 'auto';
      }
  
      return () => {
        document.body.style.overflow = 'auto';
      }
    }, [isMenuOpen])

   return (
     <div className={styles.nav_bar}>
         <div className={styles.nav_container}>
           <div className={styles.nav_menu}>
             <div className={styles.logo_area}>
               <img src={process.env.PUBLIC_URL + '/images/logo.png'} onClick={()=> { navigate('/')}} />
             </div>
             <ul>
               <li onClick={()=> { navigate('/skin')}}>SKIN</li>
               <li onClick={()=> { navigate('/body')}}>BODY</li>
               <li onClick={()=> { navigate('/hair')}}>HAIR</li>
               <li onClick={()=> { navigate('/perfume')}}>PERFUME</li>
             </ul>
           </div>
           <div className={styles.nav_login}>
             <ul>
               <li>로그인</li>
               <li>
                  <BiShoppingBag className={styles.nav_basket} onClick={() => {
                     navigate('/cart');
                  }}/>
               </li>
               <li>
                  <FaBars className={styles.nav_hamberger} onClick={() => {
                     setIsMenuOpen(true);
                  }}/>
               </li>
             </ul>
           </div>
         </div>
         {
          <div className={`toggle_container ${isMenuOpen ? 'toggle_show' : ''}`}>
            <IoCloseOutline className='close_button' onClick={()=> {
              setIsMenuOpen(false);
            }}/>
            <ul className='toggle_inner'>
              <li onClick={()=> { navigate('/skin')}}>SKIN</li>
              <li onClick={()=> { navigate('/body')}}>BODY</li>
              <li onClick={()=> { navigate('/hair')}}>HAIR</li>
              <li onClick={()=> { navigate('/perfume')}}>PERFUME</li>
            </ul>
          </div> 
        }
      </div>
   )
 }

 function Product({product, i, navigate}) {

   let formattedPrice = product.price.toLocaleString('ko-KR', { style: 'currency', currency : 'KRW'});
   let dispatch = useDispatch();

   return (
      <li>
         <div class={styles.skin_product_img_area} >
            <img src={product.images} onClick={() => {
               navigate(`/skin/${product.title}`)
            }}/>
            {/* <img src={process.env.PUBLIC_URL + '/images/skinpage/skin' + (i + 1) + '.png'} /> */}
            <div className={styles.add_cart} >
               <button className={styles.add_button} onClick={() => {
                  if (product) {
                     dispatch(setProductDetail(product))
                  }else {
                     console.log('실패');
                  }
               }}>Add Cart</button>
            </div>
         </div>
         <div class={styles.skin_img_text}>
            <em>{product.sub}</em>
            <p>{product.title}</p>
            <strong>{formattedPrice}</strong>
         </div>
      </li>
   )
 }

export default Skin;